import React,{useState} from "react";
import "./product.css";
import { useStateValue } from "../StateProvider";
import {Link} from 'react-router-dom'

import {store} from 'react-notifications-component'
import "animate.css"
import "react-notifications-component/dist/theme.css"
function Product({ id, title, image, price, rating }) {

  const [{basket},dispatch] = useStateValue();
  const [showDescription,setShowDescription] = useState(true)
  const addtoBasket=()=>{
    
    dispatch({
      type:'ADD_TO_BASKET',
      item:{
        id,
        title,
        image,
        price
      }
    })
  }
  return (
    <div className="product">
      
      <div className="product_info">
        <p>{title}</p>
        <p className="product_price">
          <small>$</small>
          <strong>{price}</strong>
        </p>

        <div className="product_rating">
          {Array(rating)
            .fill()
            .map((_) => (
              <img
                className="product_ratingImage"
                src="https://cdn4.iconfinder.com/data/icons/infy-basic-collection/48/star-1-256.png"
              />
            ))}
        </div>
      </div>
      <img className="product_image" src={image} />
      <button onClick={addtoBasket}>Add to Basket</button>
     
     <Link to="/checkout" className="product_gotocart">Go to cart</Link>
     
              

    </div>
  );
}

export default Product;
