import React from 'react'
import { useStateValue } from '../StateProvider'
import './basket.css'
import CheckoutProduct from './CheckoutProduct'
function Basket() {
const [{basket}, dispatch] = useStateValue();
let price=0;
    return (
        <div className= "checkout">
           <div className="basket_header">
        <div className="basket_ad">

           
            
           <img src="https://www.amazon.in/images/G/31/img20/Wireless/Xiaomi/Redmi_Note9/GW/D17052101_IN_WLME_Redmi_Note9_LandingPage_MobileHero_1242x450.jpg" 
           className="checkout_ad"/>
          
        </div>
        <div className="basket_total">
           <span>Total items: {basket.length}
               </span> 
               <span>
                   Total Price: $ 
                   {basket.map(item=>{
                        price = price+ item.price
                   })}
                        {price}
               </span>
               <span>
                  {basket.length>0?  <button className="basket_button">Checkout</button> :null }
               </span>

        </div>

           </div>

           {basket?.length===0 ?(
               <div>
                   <h2>Your shopping basket is empty</h2>
                   </div>
           ):
           (
           <div>
               <h2>Cart Items</h2> 
                {basket.map((item,i)=>{
                return     <CheckoutProduct
                    id={item.id}
                    image={item.image}
                    price={item.price}
                    title={item.title}
                    />
                })}

               </div>
                
           )
           }
        </div>
    )
}

export default Basket
