import React,{useState} from 'react'
import "./header.css"
import {Link} from 'react-router-dom'
import SearchIcon from '@material-ui/icons/Search'
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket'
import { useStateValue } from '../StateProvider'
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';

function getModalStyle() {
    const top = 50;
    const left = 50;
  
    return {
      top: `${top}%`,
      left: `${left}%`,
      transform: `translate(-${top}%, -${left}%)`,
    };
  }
  
  const useStyles = makeStyles((theme) => ({
    paper: {
      position: 'absolute',
      width: 400,
      color:'white',
      backgroundColor:'#111',
      border: '2px solid #000',
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
  }));



function Header() {
    const [{user,basket}, dispatch] = useStateValue();
    const [open,setOpen] = useState(false)
    const classes = useStyles();
    const [modalStyle] = React.useState(getModalStyle);
    return (
        <nav className="header">
             <Modal
        open={open}
        onClose={()=> setOpen(false)}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
          <div style={modalStyle} className={classes.paper}>
    <h2> Project Information</h2>
    <h5>*Fully functioning Shopping Cart System</h5>
    <h5>*Design UI from scratch</h5>
    <h5>*Implementation of Login System via Firebase Authorization</h5>
    <h5>*Implementation of Material UI</h5>
    <h5>    Mern Stack Developer - Samul Shrestha</h5>

    



    </div>
      </Modal>


<Link to ="/">
    
            <img 
            className="header_logo"
            src="http://pngimg.com/uploads/amazon/amazon_PNG11.png"
            />
    </Link> 
    <div className="header_search">

    <input type="text" className="header_searchInput"/>
          <SearchIcon className="header_searchIcon"/>  
    </div>

    <div className="header_nav">
            <Link to={user? "/logout": "/login"} className="header_link">
            <div className="header_option">
             <span className="header_optionLineOne">Hello {user?.email}</span>
                <span className="header_optionLineTwo">{user ? <div>Sign Out</div>: <div>Sign In </div> }</span>
            </div>
            
            </Link>

       
            <Link to="/" className="header_link">
            
            <div className="header_option">
                <span className="header_optionLineOne">Returns</span>
                <span className="header_optionLineTwo">Orders</span>
            </div>
            

            </Link>
        
            <div className="header_option" onClick ={()=>setOpen(true)}>
                <span className="header_optionInfo">Project</span>
                <span className="header_optionInfo">Information</span>
            </div>
            
         

            <Link to="/checkout">

                <div className="header_optionBasket">
                    <ShoppingBasketIcon/>
    <span className="header_optionLineTwo header_basketCount">{basket?.length}</span>

                </div>
            </Link>
            
            </div>





        </nav>
    )
}

export default Header
